import React from 'react';

import Section from "@components/Section";
import { Helmet } from "react-helmet"
import styled from "@emotion/styled";
import mediaqueries from "@styles/media";

export interface IGoogleAdsArticle {
  slot: number;
}

const GoogleAdsArticle: React.FC<IGoogleAdsArticle> = ({ slot }) => {
  return (
    <Section narrow>
      <SubscriptionContainer>
        <Content>
        <Helmet>
        <script async src="https://cse.google.com/cse.js?cx=0ee7ae7c110c0ccef"></script>
      </Helmet>
        <div className="gcse-search"></div>
        </Content>
      </SubscriptionContainer>
    </Section>
  );
};

export default GoogleAdsArticle;

const SubscriptionContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 50px auto 100px;
  z-index: 1;

  ${mediaqueries.tablet`
    padding: 50px 0 0;
    text-align: center;
  `}

  ${mediaqueries.phablet`
    margin: 20px auto 80px;
  `}
`;

const Content = styled.div`
  margin: 0 auto;
  width: 100%;

  ${mediaqueries.tablet`
    h3 {
      padding: 0 50px;
    }
  `}

  ${mediaqueries.phone`
    h3 {
      padding: 0 24px;
    }
  `}
`;